// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Reactstrap Imports
import { Badge } from 'reactstrap'
import moment from 'moment'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.servicename?.charAt(0) || 'T'}
    />
  )
}

const statusObj = [
  '',
  'light-primary',
  'light-warning',
  'light-success',
  'light-secondary',
  'light-secondary',
  'light-ocean',
  'light-info',
  'light-danger'
]

const priorityOptions = ['', 'Low', 'Medium', 'High']
const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']

const dateFormat = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }
}


export const columns = [
  {
    name: 'Task ID',
    sortable: true,
    minWidth: '138px',
    sortField: 'uniqueidentity',
    selector: row => row.uniqueidentity,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link
            to={`/task/view/${row.id}`}
            className='user_name text-truncate text-body'>
            <span className='fw-bolder'>{row.uniqueidentity}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Client',
    sortable: true,
    minWidth: '172px',
    sortField: 'clientname',
    selector: row => row.clientname,
    cell: row => <span className='text-capitalize'>{row.clientname}</span>
  },
  {
    name: 'Service',
    minWidth: '172px',
    sortable: true,
    sortField: 'servicename',
    selector: row => row.servicename,
    cell: row => <span className='text-capitalize'>{row.servicename}</span>
  },
  {
    name: 'Priority',
    minWidth: '230px',
    sortable: true,
    sortField: 'priority',
    selector: row => row.priority,
    cell: row => <span className='text-capitalize'>{priorityOptions[row.priority]}</span>
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'taskstatus',
    selector: row => row.taskstatus,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[parseInt(row.taskstatus)]} pill>
        {statusOptions[row.taskstatus]}
      </Badge>
    )
  },
  {
    name: 'End Date',
    minWidth: '138px',
    sortable: true,
    sortField: 'enddate',
    cell: row => (
      <span>{row.enddate !== null ? dateFormat(row.enddate) : ''}</span>
    )
  }
]
