// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getTaskList, exportTaskList } from './store/index'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Download, MoreVertical } from 'react-feather'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Utils
import { selectThemeColors } from '@utils'

// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Input,
    Label,
    CardBody,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useLocation, useParams } from 'react-router-dom'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, exportList }) => {
    // ** Converts table to CSV

    return (
        <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
            <Row>
                <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
                    <div className='d-flex align-items-center w-100'>
                        <label htmlFor='rows-per-page'>Show</label>
                        <Input
                            className='mx-50'
                            type='select'
                            id='rows-per-page'
                            value={rowsPerPage}
                            onChange={handlePerPage}
                            style={{ width: '5rem' }}
                        >
                            <option value='10'>10</option>
                            <option value='25'>25</option>
                            <option value='50'>50</option>
                        </Input>
                        <label htmlFor='rows-per-page'>Entries</label>
                    </div>
                </Col>
                <Col
                    xl='6'
                    className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
                >
                    <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
                        <label className='mb-0' htmlFor='search-invoice'>
                            Search:
                        </label>
                        <Input
                            id='search-invoice'
                            className='ms-50 w-100'
                            type='text'
                            value={searchTerm}
                            onChange={e => handleFilter(e.target.value)}
                        />
                    </div>

                    <div className='d-flex align-items-center table-header-actions'>

                        <UncontrolledDropdown className='ms-1'>
                            <DropdownToggle color='secondary' caret outline>
                                <MoreVertical className='font-small-4 me-50' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem onClick={() => { exportList() }} className='w-100'>
                                    <Download className='font-small-4 me-50' />
                                    <span className='align-middle'>Export</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>

                    </div>
                </Col>
            </Row>
        </div>
    )
}

const TaskList = () => {
    // ** Store Vars
    const dispatch = useDispatch()
    const store = useSelector(state => state.taskreport)
    const { filter, id } = useParams()
    // ** States
    const statusOptions = [
        { value: '', label: 'Select Status' },
        { value: '1', label: 'Pending' },
        { value: '2', label: 'In Progress' },
        { value: '3', label: 'Completed' },
        { value: '4', label: 'On Hold' },
        { value: '5', label: 'Cancelled' },
        { value: '6', label: 'Sent to Review' },
        { value: '7', label: 'Request Changes' }
    ]
    const search = useLocation().search
    const searchParams = new URLSearchParams(search)
    const [sort, setSort] = useState('desc')
    const [searchTerm, setSearchTerm] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [sortColumn, setSortColumn] = useState('createdon')
    const [rowsPerPage, setRowsPerPage] = useState(10)
    const [currentClient, setCurrentClient] = useState({ id: filter === 'clients' ? id : '', name: 'Select Client' })
    const [currentStatus, setCurrentStatus] = useState({ value: searchParams.get('status') || '', label: 'Select Status' })
    const [currentUser, setCurrentUser] = useState({ id: filter === 'users' ? id : '', name: 'Select Employee' })
    const currentService = { id: filter === 'services' ? id : '', name: 'Select Service' }
    const [filterDate, setFilterDate] = useState([])

    useEffect(() => {
        dispatch(
            getTaskList({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                user: currentUser.id,
                client: currentClient.id,
                recurringFlag: false,
                dates: filterDate,
                status: currentStatus.value,
                service: currentService.id
            })
        )
    }, [])

    const [clientOptions, setClientOptions] = useState([])
    const [userOptions, setuserOptions] = useState([])

    const getClients = () => {
        axios.post('/clients/dropdown', { status: 1 }).then(response => {
            const arr = response.data.clients
            arr.unshift({ id: '', name: 'Select Client' })
            setClientOptions(arr)
            const temp = arr.find((obj) => obj.id === id)
            setCurrentClient(temp)
        })
    }

    const getOrganizationUsers = async () => {
        axios.post('/organizationusers/dropdown', { status: 1 }).then(response => {
            const arr = response.data.organizationusers
            arr.unshift({ id: '', name: 'Select Employee' })
            setuserOptions(arr)
            const temp = arr.find((obj) => obj.id === id)
            setCurrentUser(temp)
        }).catch((err) => {
            console.log(err)
        })

    }

    useEffect(() => {
        if (filter === 'users') {
            getOrganizationUsers()
        }
        if (filter === 'clients') {
            getClients()
        }
    }, [filter])


    // ** Function in get data on page change
    const handlePagination = page => {
        dispatch(
            getTaskList({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: rowsPerPage,
                page: page.selected + 1,
                client: currentClient.id,
                status: currentStatus.value,
                recurringFlag: false,                
                user: currentUser.id,
                dates: filterDate,
                service: currentService.id

            })
        )
        setCurrentPage(page.selected + 1)
    }

    // ** Function in get data on rows per page
    const handlePerPage = e => {
        const value = parseInt(e.currentTarget.value)
        dispatch(
            getTaskList({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: value,
                user: currentUser.id,
                page: currentPage,
                client: currentClient.id,
                user: currentUser.id,
                recurringFlag: false,
                dates: filterDate,
                status: currentStatus.value
            })
        )
        setRowsPerPage(value)
    }

    const exportList = async () => {
        await dispatch(
            exportTaskList({
                sort,
                sortColumn,
                q: searchTerm,
                client: currentClient.id,
                user: currentUser.id,
                recurringFlag: false,
                user: currentUser.id,
                export: true,
                status: currentStatus.value
            })
        )
    }

    // ** Function in get data on search query change
    const handleFilter = val => {
        setSearchTerm(val)
        dispatch(
            getTaskList({
                sort,
                q: val,
                sortColumn,
                page: currentPage,
                perPage: rowsPerPage,
                client: currentClient.id,
                status: currentStatus.value,
                recurringFlag: false,
                user: currentUser.id,
                dates: filterDate,
                service: currentService.id
            })
        )
    }

    // ** Custom Pagination
    const CustomPagination = () => {
        const count = Number(Math.ceil(store.total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    // ** Table data to render
    const dataToRender = () => {
        const filters = {
            client: currentClient.id,
            user: currentUser.id,
            status: currentStatus.value,
            user: currentUser.id,
            service: currentService.id,
            recurringFlag: false,
            q: searchTerm
        }

        const isFiltered = Object.keys(filters).some(function (k) {
            return filters[k].length > 0
        })

        if (store.taskList.length > 0) {
            return store.taskList
        } else if (store.taskList.length === 0 && isFiltered) {
            return []
        }
    }

    const handleSort = (column, sortDirection) => {
        setSort(sortDirection)
        setSortColumn(column.sortField)
        dispatch(
            getTaskList({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                client: currentClient.id,
                status: currentStatus.value,
                service: currentService.id,
                user: currentUser.id,
                recurringFlag: false,
                dates: filterDate
            })
        )
    }

    return (
        <Fragment>
            <Card>
                <CardBody>
                    <Row>
                        {filter === 'clients' &&
                            <Col md='3'>
                                <Label for='role-select'>Client</Label>
                                <Select
                                    label={'name'}
                                    isClearable={false}
                                    value={currentClient}
                                    options={clientOptions}
                                    className='react-select'
                                    classNamePrefix='select'
                                    theme={selectThemeColors}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                    onChange={data => {
                                        setCurrentClient(data)
                                        dispatch(
                                            getTaskList({
                                                sort,
                                                sortColumn,
                                                q: searchTerm,
                                                client: data.id,
                                                page: currentPage,
                                                perPage: rowsPerPage,
                                                status: currentStatus.value,
                                                service: currentService.id,
                                                user: currentUser.id,
                                                recurringFlag: false,
                                                dates: filterDate
                                            })
                                        )
                                    }}
                                />
                            </Col>
                        }
                        {filter === 'users' &&
                            <Col md='3'>
                                <Label for='role-select'>Emloyee</Label>
                                <Select
                                    isClearable={false}
                                    value={currentUser}
                                    options={userOptions}
                                    className='react-select'
                                    classNamePrefix='select'
                                    theme={selectThemeColors}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                    onChange={data => {
                                        setCurrentUser(data)
                                        dispatch(
                                            getTaskList({
                                                sort,
                                                sortColumn,
                                                q: searchTerm,
                                                client: currentClient.id,
                                                page: currentPage,
                                                perPage: rowsPerPage,
                                                user: data.id,
                                                status: currentStatus.value,
                                                service: currentService.id,
                                                recurringFlag: false,
                                                dates: filterDate
                                            })
                                        )
                                    }}
                                />
                            </Col>
                        }
                        {searchParams.get('status') &&
                            <Col md='3'>
                                <Label for='status-select'>Status</Label>
                                <Select
                                    theme={selectThemeColors}
                                    isClearable={false}
                                    className='react-select'
                                    classNamePrefix='select'
                                    options={statusOptions}
                                    value={currentStatus}
                                    onChange={data => {
                                        setCurrentStatus(data)
                                        dispatch(
                                            getTaskList({
                                                sort,
                                                sortColumn,
                                                q: searchTerm,
                                                client: currentClient.id,
                                                page: currentPage,
                                                perPage: rowsPerPage,
                                                user: currentUser.id,
                                                status: data.value,
                                                recurringFlag: false,
                                                dates: filterDate
                                            })
                                        )
                                    }}
                                />
                            </Col>
                        }
                        <Col md='3'>
                            <Label for='status-select'>Date</Label>
                            <Flatpickr
                                onChange={(date) => {

                                    if (date.length === 2) {
                                        setFilterDate(date)
                                        dispatch(
                                            getTaskList({
                                                sort,
                                                sortColumn,
                                                q: searchTerm,
                                                client: currentClient.id,
                                                page: currentPage,
                                                perPage: rowsPerPage,
                                                user: currentUser.id,
                                                status: currentStatus.value,
                                                service: currentService.id,
                                                recurringFlag: false,
                                                dates: date
                                            })
                                        )
                                    }
                                }}
                                options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U", mode: 'range' }}
                                className='due-date-picker' />
                        </Col>
                    </Row>
                </CardBody>
            </Card>

            <Card className='overflow-hidden'>
                <div className='react-dataTable'>
                    <DataTable
                        noHeader
                        subHeader
                        sortServer
                        pagination
                        responsive
                        paginationServer
                        columns={columns}
                        onSort={handleSort}
                        sortIcon={<ChevronDown />}
                        className='react-dataTable'
                        paginationComponent={CustomPagination}
                        data={dataToRender()}
                        subHeaderComponent={
                            <CustomHeader
                                store={store}
                                searchTerm={searchTerm}
                                rowsPerPage={rowsPerPage}
                                handleFilter={handleFilter}
                                handlePerPage={handlePerPage}
                                exportList={exportList}
                            />
                        }
                    />
                </div>
            </Card>


        </Fragment>
    )
}

export default TaskList
